<template>
  <div class="orderList">
    <div class="navList">
      <van-tabs
        v-model="active"
        :line-width="20"
        :line-height="3"
        :swipe-threshold="6"
        :title-active-color="'#ef5852'"
        @click="onClick"
      >
        <van-tab
          v-for="(item,index) in orderClassifyList"
          :key="index"
          :title="item.label"
        ></van-tab>
      </van-tabs>
      <div class="mid">
        <div
          class="list mescroll"
          id="list"
        >
          <div
            class="list-data"
            id="listData"
          >
            <div
              class="item"
              v-for="(item,index) in list"
              :key="index"
            >
              <div
                class="item-top flex"
                @click="toDetail(item.id)"
              >
                <div class="No flex-2">:{{item.order_no}}</div>
                <div
                  class="status"
                  :style="item.order_status == 1 || item.order_status == 3?'color:#eb5252':'color:#999'"
                >{{$utils.getText(item.order_status)}}</div>
              </div>

              <div
                class="orderInfo"
                @click="toDetail(item.id)"
              >
                <div class="verificationCode">:{{item.write_off_code}}</div>
                <div class="price">{{item.actual_amount.toFixed(2)}}</div>
              </div>
              <div class="item-bottom">
                <div
                  class="btn"
                  @click="toDetail(item.id)"
                ></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Tab, Tabs, Dialog } from "vant";
// import { getShopOrderList } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs
  },
  data() {
    return {
      active: 0,
      activeValue: "",
      orderClassifyList: [
        { label: "", value: "" },
        { label: "", value: "1" },
        { label: "", value: "2" },
        { label: "", value: "5" }
      ],
      mescroll: null,
      list: [],
      orderId: "",
      orderNo: ""
    };
  },
  methods: {
    onClick(name, title) {
      this.activeValue = this.orderClassifyList[name].value;
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    toDetail(id) {
      this.$router.push({
        name: "merchantsActivityOrderDetail",
        query: { id: id }
      });
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        token:
          sessionStorage.getItem("token") ||
          "177128e71cd2d4bc4acdba48bcae04709",
        pageNum: pageNum,
        pageSize: pageSize,
        type: this.activeValue
      };
      getShopOrderList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.errorMsg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //vuemountedmescroll,id
        up: {
          callback: this.upCallback, //
          //,
          isBounce: false, //ios,(,): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //8,10
          empty: {
            //
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: ""
            //						  	btntext : " >" ,
            //						  	btnClick : function() {
            //						  		alert("");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">--  --</p>',
          lazyLoad: {
            use: true // ,false
          }
        }
      });
    },
    // page = {num:1, size:10}; num: ,1; size:,10
    upCallback: function(page) {
      //
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //,empty

          // (clearIdclearEmptyId)
          console.log(curPageData);
          if (page.num == 1) $this.list = [];
          if (curPageData.rows) {
            curPageData.rows.map(item => {
              $this.list.push(item);
            });
          }
          //,;
          //mescroll,,;,;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //():  totalSize
          $this.mescroll.endBySize(
            curPageData.rows ? curPageData.rows.length : 0,
            curPageData.recordCount
          ); //(, )
        },
        function() {
          //,;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = "";
    this.active = this.$route.query.index || 0;
    this.activeValue = this.orderClassifyList[this.active].value;
    this.startMescroll();
  }
};
</script>
<style lang="scss" scoped>
.orderList {
  .navList {
    margin-bottom: 10px;
  }
  .mid {
    height: calc(100vh - 44px);
  }
  .list {
    border-top: 1pt solid #efefef;
    .item {
      padding: 15px 10px;
      border-bottom: 1pt solid #efefef;
      background: #fff;
      .flex {
        display: flex;
        .flex-1 {
          flex: 1;
        }
        .flex-2 {
          flex: 2;
          position: relative;
        }
      }
      .item-top {
        font-size: 15px;
        line-height: 20px;
        .No {
          color: #333;
        }
      }
      .item-goods {
        margin: 15px 0;
        .goods-pic {
          width: 100px;
          height: 100px;
          margin-right: 10px;
          position: relative;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .title {
          font-size: 16px;
          line-height: 25px;
          max-height: 50px;
          text-overflow: -o-ellipsis-lastline;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .parameter {
          color: #999;
          font-size: 15px;
          line-height: 20px;
          margin: 5px 0 10px;
        }
        .price {
          font-size: 13px;
          font-weight: 600;
          color: #333;
          flex: 2;
          position: absolute;
          width: 100%;
          bottom: 10px;
          .money {
            font-size: 16px;
          }
          .num {
            color: #999;
            margin-left: 10px;
          }
        }
      }
      .orderInfo {
        font-size: 15px;
        .verificationCode {
          font-size: 16px;
          line-height: 30px;
          padding: 10px 0;
        }
        .price {
          font-size: 14px;
          text-align: right;
          color: #666;
        }
      }
      .item-bottom {
        text-align: center;
        overflow: hidden;
        padding: 15px 0;
        .btn {
          width: 80px;
          color: #666;
          border: 1pt solid #e5e5e5;
          float: right;
          height: 30px;
          line-height: 30px;
          border-radius: 5px;
          font-size: 13px;
          margin-left: 5px;
        }
        .btnPay {
          background: #fff;
          color: $base-color;
          border: 1pt solid $base-color;
        }
      }
    }
  }
}
</style>
